@import url('https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@300;400;500;700&family=Noto+Sans+KR:wght@300;400;700&display=swap');
@import './_variable.scss';
// $grey: #666666;
// $black: #000000;
// $white: #ffffff;
// $desktop-container-width: 1200px;

* {
    box-sizing: border-box;
}

html {
    // * width 375일때 기본 폰트 사이즈 10px
    // font-size: 62.5%;
    font-size: 2.666666vw;
    // * width 320일때 기본 폰트 사이즈 10px
    // font-size: 3.125vw;
    // font-size: 0.652vw;
    height: 100%;
    overflow: hidden;
    -webkit-font-smoothing: antialiased;
}

body {
    height: 100%;
    margin: 0;
    /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
    font-family: 'Kumbh Sans', 'Noto Sans KR', -apple-system, BlinkMacSystemFont, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    line-height: 1;
    letter-spacing: -0.01em;
    color: $black;
    -ms-overflow-style: none;
    overflow: hidden;
    -ms-scroll-chaining: none;
    overscroll-behavior: none;
}

figure {
    margin: 0;
}

// [data-scroll-container] {
.page-container {
    height: 100%;
    // overflow: hidden auto;
    -ms-scroll-chaining: none;
    overscroll-behavior: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    &.menu-open,
    &.scroll-disabled {
        overflow: hidden;
    }
}

.site-content {
    height: 100%;
    width: 100%;
}

// ::-webkit-scrollbar {
//     display: none;
// }

img {
    max-width: 100%;
}
button,
input,
textarea,
select {
    font-family: 'Kumbh Sans', 'Noto Sans KR', -apple-system, BlinkMacSystemFont, sans-serif;
    color: inherit;
    appearance: none;
}
textarea {
    overflow-y: scroll;
}
::-webkit-scrollbar-thumb {
    width: 0px;
    height: 0px;
    background-color: rgba(255, 255, 255, 0.35);
}
textarea::-webkit-scrollbar {
    background-color: rgba(0, 0, 0, 0.3);
    width: 3px;
}
input::-webkit-scrollbar-thumb,
textarea::-webkit-scrollbar-thumb {
    width: 2px;
    height: 1px;
    background-color: #ececec;
    border-radius: 3px;
}

[data-theme] {
    transition: color 1s cubic-bezier(0, 0, 0.2, 1), background-color 1s ease;
    will-change: color, background-color;
}

[data-theme='white'] {
    color: $white;
}
[data-theme='black'] {
    color: $black;
}

.grid-inner {
    width: 100%;
    padding: 0 3rem;
    box-sizing: border-box;
    display: flex;
    flex-wrap: wrap;
}
#container {
    overflow: hidden auto;
    height: 100%;
}
.contents {
    padding-top: 10.7rem;
}
.ProjectDetail .contents {
    padding-top: 0;
}

.contents .page-title {
    font-size: 3rem;
    font-weight: 700;
}

.main-section {
    // height: 100vh;
    height: $mobile-vh;
    overflow: hidden;
}
.section-hero {
    background-color: #6f6f6f;
    // ! dev code
    // width: 100vw;
    // height: 100vh;
    // border: 1px solid red;
    // position: relative;
    > div {
        height: 100vw;
        -webkit-transform: translate(calc(50vw - 50%),calc(var(--vh, 1vh) * 50 - 50%)) rotate(90deg);
        transform: translate(calc(50vw - 50%),calc(var(--vh, 1vh) * 50 - 50%)) rotate(90deg);
        // width: 100vh;
        width: calc(var(--vh, 1vh) * 100);
        // position: absolute;
        // height: 100%;
        // width: 100%;
        // top: 0;
        // left: 0;
        // right: 0;
        // bottom: 0;
        > video {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    // ! dev code
}

.typo-line {
    white-space: nowrap;
    width: 100%;
    display: flex;
    justify-content: flex-start;
}
.typo-slide span {
    display: inline-block;
    margin-right: 6rem;
    text-transform: uppercase;
}
.typo-line.flow-ltr span {
    margin-right: 0;
    margin-left: 6rem;
}
.typo-line.flow-ltr {
    justify-content: flex-end;
}

.section-container {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    -webkit-transform: translateZ(0);
    will-change: transform;
}
.section-work {
    position: relative;
    // padding-bottom: 9.2rem;
    .work-scroll-container {
        // background-color: #AF2833;
        height: 100%;
    }
    .work-slide-wrapper {
        position: relative;
        height: 100%;
        // padding: 39.5vh 0 18.3vh;
        padding: calc(calc(var(--vh, 1vh) * 100) * 0.395) 0 calc(calc(var(--vh, 1vh) * 100) * 0.183);
        // padding: 26.3rem 4.7rem 12.2rem;
    }
    .wrapper-inner {
        height: 100%;
        overflow: hidden;
        position: relative;
    }
    .wrapper-inner .slide-contents {
        height: 100%;
    }
    .work-slide {
        // position: absolute;
        // top: 0;
        // left: 0;
        // width: 100%;
        display: flex;
        justify-content: center;
        width: calc(calc(calc(var(--vh, 1vh) * 100) - calc(calc(var(--vh, 1vh) * 100) * 0.578)));
        // width: calc(calc(var(--vh, 1vh) * 100) * 0.578);
        margin: 0 auto;
        height: 100%;
        padding-bottom: 1.5rem;
    }
    .work-slide figure > div {
        position: relative;
        height: 100%;
        width: 100%;
        // padding-bottom: 3rem;
    }
    .work-slide img {
        display: block;
        height: 100%;
        max-width: none;
        perspective: 1000;
        position: relative;
        z-index: 1;
        backface-visibility: hidden;
        transform: translate3d(0, 0, 0);
        -webkit-perspective: 1000;
        -webkit-backface-visibility: hidden;
        -webkit-transform: translate3d(0, 0, 0);
    }
    .work-slide figure { 
        // width: auto;
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: column;
        position: relative;
    }
    .work-slide figcaption {
        position: absolute;
        // bottom: 0.2rem;
        // bottom: 0.2rem;
        left: 0;
        bottom: -1.5rem;
        font-size: 1rem;
        line-height: 1.2;
        font-weight: 700;
    }
    .work-info-typo {
        position: absolute;
        top: calc(calc(var(--vh, 1vh) * 100) * 0.136);
        left: 0;
        // transform: translate(0, -50%);
    }
    .work-info-typo .typo-element {
        overflow: hidden;
        width: 100vw;
        // position: absolute;
        // top: 50%;
        // left: 0;
        // transform: translate(0, -50%);
        font-size: 0;
        // opacity: 0;
    }
    .work-info-typo .typo-element .typo-line {
        font-size: 10rem;
        font-weight: 500;
        letter-spacing: -0.1rem;
    }
    .work-info-typo .typo-element .typo-wrap {
        overflow: hidden;
    }
    .work-info-typo .typo-element .typo-line .typo-slide {        
        // -webkit-animation-timing-function: linear;
        // animation-timing-function: linear;
        // -webkit-animation-iteration-count: infinite;
        // animation-iteration-count: infinite;
        will-change: transform;
    }
    .work-info-typo .typo-element .typo-line-01 {
        margin-bottom: -1rem;
    }
    .work-info-typo .typo-element .typo-line-02 {
        font-size: 11rem;
        -webkit-text-stroke: 1px #ffffff;
        -webkit-text-fill-color: transparent;
        // margin-top: -1rem;
    }
    // .work-slide .work-img {
    //     background-size: cover;
    //     background-repeat: no-repeat;
    //     width: 100%;
    //     padding-bottom: 100%;
    // }
}

.section-work > h3 {
    font-size: 1.5rem;
    text-align: center;
    padding: 4.5rem 0 2rem;
    font-weight: 300;
    color: $black;
}

.section-about {
    background-color: #000000;
    .about-typo-wrap {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
        .typo-slide {
            span {
                font-size: 11.5rem;
                font-weight: 500;
                line-height: 0.9;
            }
        }
    }
}


