#container.Contact {
    background-color: $black;
    color: $white;
    width: 100%;
    // height: 100vh;
    height: calc(var(--vh, 1vh) * 100);
    > .inner {
        width: 100%;
        height: 100%;
        // border: 1px solid red;
        padding: 0 3rem;
        position: relative;
        display: flex;
        align-items: center;
        .surveys {
            width: 100%;
            .survey-item {
                .title-block {
                    margin-bottom: 8rem;
                    .title-num {
                        font-weight: 700;
                        font-size: 1.8rem;
                        line-height: 2.1rem;
                        letter-spacing: -0.03em;
                        margin-bottom: 1rem;
                    }
                    .title {
                        font-weight: 700;
                        font-size: 2rem;
                        line-height: 2.5rem;
                        letter-spacing: -0.03em;
                        margin-bottom: 0.6rem;
                    }
                    .title-desc {
                        font-weight: 400;
                        font-size: 1.1rem;
                        line-height: 2.2rem;
                        color: #898989;
                        letter-spacing: -0.03em;
                    }
                }
                &[data-idx='1'] .title-block,
                &[data-idx='2'] .title-block {
                    margin-bottom: 4rem;
                }
                &[data-idx='6'] .title-block {
                    .title {
                        margin-bottom: 2.4rem;
                    }
                }
                &[data-idx='5'] input {
                    margin-bottom: 0.8rem;
                }
                .input-block {
                    // padding-right: 33%;
                    > .inner {
                        box-sizing: border-box;
                        display: flex;
                        flex-wrap: wrap;
                        .input-box {
                            > .inner {
                                display: inline-block;
                                &.full-width {
                                    width: 100%;
                                }
                            }
                            width: 100%;
                            margin-bottom: 1rem;
                            input {
                                font-weight: 400;
                                font-size: 1.4rem;
                                line-height: 1.7rem;
                                color: $white;
                                width: 20rem;
                                border-bottom: 1px solid #d9d9d9;
                                padding: 1rem;
                                &:focus {
                                    outline: none;
                                }
                            }
                            input[type='text'] {
                                width: 20rem;
                                // height: 3.6rem;
                                border-bottom: 1px solid #d9d9d9;
                                padding: 1rem;
                            }
                            textarea {
                                font-weight: 400;
                                font-size: 1.4rem;
                                line-height: 1.7rem;
                                width: 100%;
                                height: 20rem;
                                resize: none;
                                padding: 1rem;
                                border-top: 1px solid #d9d9d9;
                                border-bottom: 1px solid #d9d9d9;
                                &:focus {
                                    outline: none;
                                }
                                &::placeholder {
                                    font-weight: 400;
                                    font-size: 1.4rem;
                                    line-height: 1.7rem;
                                }
                            }
                            label {
                                display: inline-block;
                                cursor: pointer;
                                > input {
                                    opacity: 0;
                                    visibility: hidden;
                                    position: absolute;
                                    top: 0;
                                    left: 0;
                                    &:checked + .btn-checkbox {
                                        background-color: $white;
                                        color: $black;
                                        transition: color 0.3s cubic-bezier(0, 0, 0.2, 1), background-color 0.3s ease;
                                        will-change: color, background-color;
                                    }
                                }
                                .btn-checkbox {
                                    padding: 1rem 3rem 1rem 2.5rem;
                                    border: 1px solid $white;
                                    color: $white;
                                    border-radius: 6rem;
                                    font-weight: 400;
                                    font-size: 1.4rem;
                                    line-height: 1.7rem;
                                    display: inline-block;
                                }
                            }
                            &:last-of-type {
                                margin-bottom: 0;
                            }
                            &#dateInput {
                                // border: 1px solid red;
                                > .inner {
                                    display: flex;
                                    align-items: center;
                                    width: fit-content;
                                    border-bottom: 1px solid #d9d9d9;
                                    > span {
                                        font-weight: 400;
                                        font-size: 1.4rem;
                                        line-height: 1.7rem;
                                        color: rgba(255, 255, 255, 0.3);
                                    }
                                    .react-datepicker-wrapper {
                                        width: auto;
                                        // input {
                                        //     width: 9.6rem;
                                        //     text-align: center;
                                        //     border-bottom: 0;
                                        // }
                                        button {
                                            font-weight: 400;
                                            font-size: 1.4rem;
                                            line-height: 1.7rem;
                                            // color: #ffffff;
                                            color: rgba(255, 255, 255, 0.3);
                                            padding: 1rem;
                                            width: 9.6rem;
                                            text-align: center;
                                            border-bottom: 0;
                                            &.selected {
                                                color: $white;
                                            }
                                        }
                                    }
                                    .react-datepicker__input-container {
                                        button {
                                            font-weight: 400;
                                            font-size: 1.4rem;
                                            line-height: 1.7rem;
                                            // color: #ffffff;
                                            color: rgba(255, 255, 255, 0.3);
                                            padding: 1rem;
                                            width: 9.6rem;
                                            text-align: center;
                                            border-bottom: 0;
                                            &.selected {
                                                color: $white;
                                            }
                                        }
                                    }
                                    .react-datepicker__portal {
                                        height: calc(var(--vh, 1vh) * 100);
                                    }
                                    .react-datepicker-popper,
                                    .react-datepicker__portal {
                                        &[data-placement^='bottom'] .react-datepicker__triangle::after {
                                            border-bottom-color: $black;
                                        }
                                        &[data-placement^='top'] .react-datepicker__triangle::after {
                                            border-top-color: $black;
                                        }
                                        // &[data-placement^=top] .react-datepicker__triangle::before {
                                        //     border-top-color: ;
                                        // }
                                        .react-datepicker {
                                            background-color: $black;
                                            color: $white;
                                            .react-datepicker__day-name,
                                            .react-datepicker__day,
                                            .react-datepicker__time-name {
                                                color: $white;
                                                font-size: 1.6rem;
                                                margin: 0.8rem;
                                                font-weight: 400;
                                                width: 2.2rem;
                                                line-height: 2.2rem;
                                            }
                                            .react-datepicker__day--selected,
                                            .react-datepicker__day--in-selecting-range,
                                            .react-datepicker__day--in-range,
                                            .react-datepicker__month-text--selected,
                                            .react-datepicker__month-text--in-selecting-range,
                                            .react-datepicker__month-text--in-range,
                                            .react-datepicker__quarter-text--selected,
                                            .react-datepicker__quarter-text--in-selecting-range,
                                            .react-datepicker__quarter-text--in-range,
                                            .react-datepicker__year-text--selected,
                                            .react-datepicker__year-text--in-selecting-range,
                                            .react-datepicker__year-text--in-range {
                                                background-color: $white;
                                                color: $black;
                                            }
                                            .react-datepicker__day--keyboard-selected,
                                            .react-datepicker__month-text--keyboard-selected,
                                            .react-datepicker__quarter-text--keyboard-selected,
                                            .react-datepicker__year-text--keyboard-selected {
                                                background-color: $white;
                                                color: $black;
                                            }
                                            .react-datepicker__day--disabled,
                                            .react-datepicker__month-text--disabled,
                                            .react-datepicker__quarter-text--disabled,
                                            .react-datepicker__year-text--disabled {
                                                color: $grey2;
                                            }
                                            .react-datepicker__year-read-view--down-arrow,
                                            .react-datepicker__month-read-view--down-arrow,
                                            .react-datepicker__month-year-read-view--down-arrow,
                                            .react-datepicker__navigation-icon::before {
                                                border-width: 0.2rem 0.2rem 0 0;
                                                height: 0.6rem;
                                                width: 0.6rem;
                                                top: calc(50% - 0.3rem);
                                            }
                                            .react-datepicker__navigation-icon--next::before {
                                                left: -0.5rem;
                                            }
                                            .react-datepicker__navigation-icon--previous::before {
                                                right: -0.5rem;
                                            }
                                            .react-datepicker__navigation {
                                                width: 4.2rem;
                                                height: 4.2rem;
                                                top: 0;
                                                // border: 0.1rem solid;
                                                .react-datepicker__navigation-icon {
                                                    top: 0;
                                                    font-size: 1.4rem;
                                                }
                                            }
                                            .react-datepicker__navigation--next {
                                                right: 0;
                                            }
                                            .react-datepicker__navigation--previous {
                                                left: 0;
                                            }
                                            .react-datepicker__month-container {
                                                .react-datepicker__header {
                                                    background-color: $black;
                                                    color: $white;
                                                    padding: 1.4rem 0;
                                                    .react-datepicker__current-month {
                                                        font-size: 1.6rem;
                                                        color: $white;
                                                        margin-bottom: 0.6rem;
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                            &#budgetInput {
                                > .inner {
                                    position: relative;
                                    span {
                                        position: absolute;
                                        font-weight: 400;
                                        font-size: 1.4rem;
                                        line-height: 1.7rem;
                                        color: rgba(255, 255, 255, 0.3);
                                        top: calc(50% - 0.85rem);
                                        right: 1rem;
                                    }
                                    input {
                                        padding: 1rem 5rem 1rem 1rem;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
        .btn-next {
            position: absolute;
            right: 2.8rem;
            bottom: 3.1rem;
            width: 4.4rem;
            height: 4.4rem;
            display: flex;
            justify-content: center;
            align-items: center;
            // svg {
            //     margin-top: -0.6rem;
            // }
            svg {
                width: 100%;
                height: 100%;
            }
        }
    }
}

