#container.ProjectDetail {
    .play_trigger {
        cursor: pointer;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0, 0, 0, 0.3);
        .play_icon {
            display: block;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 2.1rem;
            height: 2.4rem;
            transition: ease-in-out 0.2s;
        }
    }
    .small-title {
        font-size: 1.6rem;
        font-weight: 700;
    }
    .project-detail__top-block {
        position: relative;
        // padding: 1rem 3rem 3rem;
        padding: 0 3rem;
        // opacity: 0;
        .small-title {
            margin-bottom: 1.2rem;
        }
        .project-detail__title-kr {
            font-size: 1.2rem;
            font-weight: 700;
            margin-bottom: 0.6rem;
        }
        .project-detail__title {
            font-size: 1.2rem;
            font-weight: 300;
        }
        .project-detail_categories {
            display: flex;
            flex-wrap: wrap;
            margin: 1.5rem 0 0;
            .project-detail__category {
                padding: 0.55rem 1.25rem;
                margin-right: 0.6rem;
                margin-bottom: 0.6rem;
                border-radius: 6rem;
                border: 0.1rem solid $black;
                font-size: 1rem;
            }
        }
        .go-list {
            position: absolute;
            top: -0.7rem;
            right: 3rem;
            font-size: 1rem;
            padding: 1rem;
            color: #9c9c9c;
            text-decoration: underline;
            text-underline-offset: 0.1rem;
        }
    }
    .project-detail__hero {
        // margin-top: 170px;
        // padding: 0 3rem;
        position: relative;
        margin: 0 auto 3.6rem;
        // padding-bottom: 72%;
        padding-bottom: 90%;
        .client-box {
            position: absolute;
            bottom: 2.4rem;
            left: 3rem;
            .client-box__title-kr {
                font-weight: 700;
                font-size: 1.2rem;
                letter-spacing: -0.03em;
                margin-bottom: 0.2rem;
            }
            .client-box__title {
                font-weight: 700;
                font-size: 2rem;
                letter-spacing: -0.01em;
            }
        }
    }
    .project-detail__hero .hero-box {
        position: absolute;
        height: 100%;
        width: 100%;
    }
    .project-detail__hero .hero-box > div {
        position: absolute;
        // top: 50%;
        // left: 50%;
        // transform: translate(-50%, -50%);
        // height: auto;
        // height: 100%;
        width: 100%;
        height: 100%;
    }
    .project-detail__hero .hero-box img,
    .project-detail__hero .hero-box video {
        height: 100%;
        object-fit: cover;
    }
    .project-detail__middle-block {
        // opacity: 0;
        padding: 2.5rem 0 3.6rem;
        > .grid-inner {
            flex-direction: column;
            margin-bottom: 3.6rem;
        }
        .project-detail__desc dl {
            margin-bottom: 2.4rem;
            &:last-child {
                margin-bottom: 0;
            }
            dt {
                margin-bottom: 1.2rem;
            }
            dd {
                font-size: 1.2rem;
                font-weight: 300;
                color: $grey2;
                line-height: 1.41;
                letter-spacing: -0.03em;
                word-break: break-all;
            }
        }
        .project-detail__details {
            margin-bottom: 3.6rem;
            .swiper-slide {
                width: auto;
                video,
